import React from 'react'
import { Route } from 'react-router-dom'
import BreadcrumbsLinks from './links'
import {
    BreadcrumbsList
} from 'styles'

const Breadcrumbs = ({ match }) => {

    return (
        <BreadcrumbsList margin="0px 0px 20px 0px">
            <Route path='/:path' component={BreadcrumbsLinks} />
        </BreadcrumbsList>
    )
}

export default Breadcrumbs

