import React, { useState } from 'react'
import Modal from 'components/modal'
import getIcon from 'components/icons'

import {
    FAQ
} from 'styles'

const Faq = ({ children }) => {
    const [isOpen, setIsOpen] = useState(false)

    return (
        <FAQ onClick={() => setIsOpen(!isOpen)} className="main">
            <img title="Информация об этой странице" src={getIcon('faq')} alt="faq" />
            {isOpen &&
                <Modal>
                    {children}
                </Modal>
            }
        </FAQ>
    )
}

export default Faq

