import React, { useState, useRef } from 'react'
import { Input, Textarea } from 'components/input'
import Button from 'components/button'
import {
    FormItem,
    Flex
} from 'styles'

const ShortcodeItem = ({ data, onDelete }) => {
    const [isDel, setisDel] = useState(false)
    const shortcode = useRef(null)
    const message = useRef(null)

    const onSubmit = (e) => {
        e.preventDefault()
        console.dir(shortcode.current.value);
        console.dir(message.current.value);
    }

    const onDeleteItem = () => {
        setTimeout(onDelete, 700, data.id)
        setisDel(true)
    }

    return (
        <FormItem onSubmit={onSubmit} className={isDel ? 'deleting' : ''}>
            <Input
                width="100%"
                padding="10px 0px"
                label="Шорткод"
                placeholder="Введите шорткод"
                margin="10px 0px"
                className="main"
                forwardRef={shortcode}
                defaultValue={data.shortcode || ''}
            />
            <Textarea
                padding="10px 0px 10px 0px"
                label="Сообщение"
                placeholder="Введите текст сообщения"
                margin="10px 0px"
                className="main"
                width="100%"
                forwardRef={message}
                defaultValue={data.message || ''}
            />
            <Flex justify="space-between" margin="10px 0px 0px">
                <Button type="button" className="danger" kind="small" onClick={onDeleteItem}>
                    Удалить
                </Button>
                <Button type="submit" className="main" kind="small">
                    Сохранить
                </Button>
            </Flex>
        </FormItem>
    )
}

export {
    ShortcodeItem
}
