import React from 'react'
import ReactDOM from 'react-dom'
import {
    Container,
    Content
} from './styles'

const Modal = ({ children = 'Content is empty', padding, onClose = () => {} }) => {

    const promptElement = document.getElementById('bag')

    return (
        ReactDOM.createPortal(
            <Container>
                <Content padding={padding} className="scyle">
                    {children}
                </Content>
            </Container>,
            promptElement
        )

    )
}

export default Modal