import React from 'react'
import { Input } from 'components/input'
import {
    NavLink
} from 'react-router-dom'

const NavLinks = () => {

    return (
        <>
            <Input
                width="100%"
                padding="0px 0px 20px 0px"
                type="search"
                title="Поиск по имени, ID, сообщению и дате"
                placeholder="Поиск"
            />
            <NavLink to="/chat" exact className="link-button main">
                Личное
            </NavLink>
            <NavLink to="/chat/groups" className="link-button main">
                Группы
            </NavLink>
        </>
    )
}

export default NavLinks