import React from 'react'
import { Route, Link } from 'react-router-dom'

const BreadcrumbsLinks = ({ match }) => {

    const titles = {
        'settings': 'Настройки',
        'shortcodes': 'Шорткоды',
        'triggers': 'Триггеры',
        'tools': 'Инструменты',
        'auto-answer': 'Автоответчик'
    }

    return (
        <>
            <Link to={match.url || ''} className={match.isExact ? 'active' : ''}>
                {/* eslint-disable-next-line */}
                {titles[match.url.match(/[^\/]*$/)] || '???'}
            </Link>
            <Route path={`${match.url}/:path`} component={BreadcrumbsLinks} />
        </>
    )
}

export default BreadcrumbsLinks

