import React, { Suspense } from 'react'
import { useStore } from 'effector-react'
import { ShortcodeItem, ShortcodeMenu, $shortcodeStore, deleteShortcode } from '.'

import {
    Grid,
    SubTitle
} from 'styles'

const NotFound = React.lazy(() => import('components/not-found'))

const ShortcodeList = () => {

    const shortcodeList = useStore($shortcodeStore)

    return (
        <>
            <ShortcodeMenu />
            {
                shortcodeList.length > 0 ?
                    <>
                        <SubTitle margin="10px 5px 5px 5px">Лист шорткодов</SubTitle>
                        <Grid width="100%" gap="20px">
                            {
                                shortcodeList.map(i => {
                                    return <ShortcodeItem key={i.id} data={i} onDelete={deleteShortcode} />
                                })
                            }
                        </Grid>
                    </>
                    :
                    <Suspense fallback={""}>
                        <NotFound title="Шорткодов не найдено" caption="Добавьте шорткод, чтобы он здесь отобразился" />
                    </Suspense>
            }
        </>
    )
}

export {
    ShortcodeList
}
