import React, { useState, Suspense } from 'react'
import { NavLink } from 'react-router-dom'
import searchItems from 'components/search'
import { Input } from 'components/input'
import getIcon from 'components/icons'
import { useStore } from 'effector-react'
import Button from 'components/button'
import { ShortcodeNew, addShortcode, $shortcodeStore, changeShortcode } from '.'
import {
    Wrapper,
} from 'styles'

const Modal = React.lazy(() => import('components/modal'))

const ShortcodeMenu = () => {
    const [ isOpenAdd, setIsOpenAdd ] = useState(false)
    const shortcodeList = useStore($shortcodeStore)

    const onChangeSearch = (e) => {
        const foundItems = searchItems(shortcodeList, ['shortcode', 'message'], e.target.value)
        changeShortcode(foundItems)
    }


    return (
        <>
            {isOpenAdd &&
                <Suspense fallback={""}>
                    <Modal onClose={setIsOpenAdd}>
                        <ShortcodeNew onAdd={addShortcode} id={shortcodeList.length} onClose={setIsOpenAdd} />
                    </Modal>
                </Suspense>
            }
            <Wrapper shadow="none" padding="0px" display="flex" align="center" justify="flex-end" margin="15px 5px">

                <Input
                    margin="0px auto 10px 0px"
                    padding="0px"
                    id="do_not_repeat"
                    type="search"
                    className="main"
                    placeholder="Поиск"
                    onChange={onChangeSearch}
                />

                <Button icon="add" kind="small" className="main link-icon" onClick={() => setIsOpenAdd(true)} />

                <NavLink to="/settings/shortcodes/tools" className="main link-icon">
                    <img loading="lazy" src={getIcon('tools')} alt="tools"/>
                </NavLink>

            </Wrapper>
        </>
    )
}

export {
    ShortcodeMenu
}
