import React, { Suspense } from 'react'
import Menu from 'components/menu'
import NavLinks from './navLinks'

import {
    Container,
} from './styles'
import {
    Wrapper
} from 'styles'

const Chats = React.lazy(() => import('./chats'))

const Chat = () => {

    return (
        <Container>
            <Menu>
                <div className="menu">
                    <NavLinks />
                </div>
                <Wrapper className="content" width="100%" shadow="none" padding="0px" justify="space-between" margin="0px 0px 0px 30px" align="flex-start">
                    <Suspense fallback={""}>
                        <Chats />
                    </Suspense>
                </Wrapper>
            </Menu>
        </Container>
    )
}

export default Chat