import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom'
import Header from 'components/header'
import Main from 'pages/main'
import LogIn from './pages/login'


const App = () => {

  return (
    <Router>
      <div className="wrapper">
        <Header />
        <Switch> 
          <Route path="/auth">
            <LogIn />
          </Route>
          <Route path="/" exact>
            Main
          </Route>
          <Main />
        </Switch>
      </div>
    </Router>

  )

}

export default App
