import React from 'react'
import { NavLink } from 'react-router-dom'
import { Input } from 'components/input'
import Button from 'components/button'
import getIcon from 'components/icons'
import {
    Wrapper,
} from 'styles'

const AutoanswerMenu = () => {

    return (
        <Wrapper shadow="none" padding="0px" display="flex" align="center" justify="flex-end" margin="15px 5px">

            <Input
                margin="0px auto 10px 0px"
                padding="0px"
                id="do_not_repeat"
                type="search"
                className="main"
                placeholder="Поиск"
            />

            <Button icon="add" kind="small" className="main link-icon" />

            <NavLink to="/settings/auto-answer/tools" className="main link-icon">
                <img loading="lazy" src={getIcon('tools')} alt="tools" />
            </NavLink>

        </Wrapper>
    )

}

export {
    AutoanswerMenu
}

