import React from 'react'
import {
    NavWrapper
} from './styles'
import {
    NavLink
} from 'react-router-dom'
import getIcon from 'components/icons'

const NavLinks = () => {

    return (
        <NavWrapper>
            <NavLink to="/settings/auto-answer" className="link-button main">
                <img loading="lazy" src={getIcon('autoAnswer')} alt="auto-answer"/>
                <span>Автоответчик</span>
            </NavLink>
            <NavLink to="/settings/triggers" className="link-button main">
                <img loading="lazy" src={getIcon('trigger')} alt="triggers"/>
                <span>Триггеры</span>
            </NavLink>
            <NavLink to="/settings/shortcodes" className="link-button main">
                <img loading="lazy" src={getIcon('shortcodes')} alt="shortcodes"/>
                <span>Шорткоды</span>
            </NavLink>
            <NavLink to="/settings/records" className="link-button main">
                <img loading="lazy" src={getIcon('records')} alt="records"/>
                <span>Записи</span>
            </NavLink>
            <NavLink to="/settings/stat" className="link-button main">
                <img loading="lazy" src={getIcon('stat')} alt="stat"/>
                <span>Статистика</span>
            </NavLink>
            <NavLink to="/settings/chat" className="link-button main">
                <img loading="lazy" src={getIcon('group')} alt="group"/>
                <span>Группы</span>
            </NavLink>
            <NavLink to="/settings/sheduler" className="link-button main">
                <img loading="lazy" src={getIcon('sheduler')} alt="sheduler"/>
                <span>Планировщик</span>
            </NavLink>
            <NavLink to="/settings/black-list" className="link-button main">
                <img loading="lazy" src={getIcon('blackList')} alt="records"/>
                <span>Черный список</span>
            </NavLink>
            <NavLink to="/settings" exact className="link-button main">
                <img loading="lazy" src={getIcon('faq')} alt="faq"/>
                <span>Подробнее</span>
            </NavLink>
        </NavWrapper>
    )
}

export default NavLinks