import React, { Suspense } from 'react'
import { useStore } from 'effector-react'
import { $autoanswerStore, AutoanswerMenu, deleteAutoanswer, AutoanswerItem } from '.'

import {
    Grid,
    SubTitle
} from 'styles'

const NotFound = React.lazy(() => import('components/not-found'))


const AutoanswerList = () => {

    const autoanswerList = useStore($autoanswerStore)


    return (
        <>
            <AutoanswerMenu />
            {
                autoanswerList.length > 0 ?
                    <>
                        <SubTitle margin="10px 5px 5px 5px">Лист автоответчиков</SubTitle>
                        <Grid width="100%" gap="20px">
                            {
                                autoanswerList.map(i => {
                                    return <AutoanswerItem key={i.id} data={i} onDelete={deleteAutoanswer} />
                                })
                            }
                        </Grid>
                    </>
                    :
                    <Suspense fallback={""}>
                        <NotFound title="Автоответчиков не найдено" caption="Добавьте автоответчик, чтобы он здесь отобразился" />
                    </Suspense>
            }
        </>
    )
}

export {
    AutoanswerList
}
