import React from 'react'
import { Textarea } from 'components/input'
import Button from 'components/button'
import {
    FormItem,
    Flex
} from 'styles'

const AutoanswerItem = () => {

    return (
        <FormItem>
            <Textarea
                padding="10px 0px"
                label="Сообщение"
                placeholder="Введите текст автоответчика"
                margin="10px 0px"
                className="main"
                width="100%"
            />
            <Flex justify="space-between" margin="10px 0px 0px">
                <Button type="button" className="danger" kind="small">
                    Удалить
                </Button>
                <Button type="submit" className="main" kind="small">
                    Сохранить
                </Button>
            </Flex>
        </FormItem>
    )
}

export {
    AutoanswerItem
}


