import React from 'react'
import getIcon from 'components/icons'
import {
    NavLink
} from 'react-router-dom'
import {
    Container,
} from './styles'


const Header = () => {

    // const getData = () => {
    //     fetch('http://192.168.10.184:3000/api/auth',
    //     {
    //         method: "GET",
    //         headers: {
    //             "Content-Type": "text/plain"
    //         }
    //     })
    //     .then(response => console.log('Response ' + response.body))
    // }

    return (
        <Container>
            <div className="logo-icon">
                <NavLink to="/">
                    <img loading="lazy" src={getIcon('logo')} alt="logo" />
                </NavLink>
            </div>
            <NavLink to="/auth" className="link-icon main">
                <img loading="lazy" src={getIcon('user')} alt="user" />
            </NavLink>
            <NavLink to="/chat" className="link-icon main">
                <img loading="lazy" src={getIcon('chat')} alt="chat" />
            </NavLink>
            <NavLink to="/settings" className="link-icon main">
                <img loading="lazy" src={getIcon('settings')} alt="settings" />
            </NavLink>
        </Container>
    )
}

export default Header
