import styled from 'styled-components'

const Container = styled.form`
    max-width: 410px;
    min-width: 320px;
    margin: 50px auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 0px 20px 10px rgba(0, 0, 0, 0.03);
    padding: 20px 0px;
    border-radius: 10px;
    animation: fade-in .3s forwards ease-in-out;
    background-color: #fff;
    @media (max-width: 500px) {
        max-width: 330px;
        min-width: auto;
        padding: 20px 10px;
    }
`

const Header = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    margin-bottom: 30px;
    width: 100%;
`

const Counter = styled.div`
    color: #5DA3C0;
    font-size: 15px;
    letter-spacing: 0.03em;
`

export {
    Container,
    Header,
    Counter
}
