import styled from 'styled-components'

const types = {
    'small': {
        padding: '10px 15px',
        fontSize: '0.9em'
    },
    'medium': {
        padding: '10px 30px',
    },
    'bold': {
        padding: '13px 35px',
        fontSize: '15px !important'
    },
    'icon': {
        padding: '13px 20px',
        fontSize: '15px !important'
    }
}

const iconsSizes = {
    'small': {
        width: '15px',
        height: '15px'
    },
    'medium': {
        width: '25px',
        height: '25px'
    },
    'bold': {
        width: '35px',
        height: '35px'
    }
}

const Container = styled.button`
    margin: ${props => props.margin ? props.margin : '0px'};
    pointer-events: ${props => props.loading === 'true' ? 'none' : 'auto'};
    width: ${props => props.width ? props.width : 'auto'};
    ${props => props.icon && `
        display: flex;
        align-items: center;
    `}
    /* opacity: ${props => props.loading === 'true' ? '.5' : '1'}; */
    ${props => props.loading === 'true' && `
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 5px;
            background-color: inherit;
            z-index: -1;
        }
        & span {
            animation: fade-in .3s forwards ease-in-out;
        }
    `}
    ${props => props.kind && types[props.kind] ? {...types[props.kind]} : {...types['medium']}};
    padding: ${props => props.padding ? props.padding : props.kind && types[props.kind] ? types[props.kind].padding : types.medium.padding};
    & img {
        ${props => props.iconSize && iconsSizes[props.iconSize] ? {...iconsSizes[props.iconSize]} : {...iconsSizes['medium']}};
        ${props => props.iconRight ? `
            margin-left: 15px;
            order: 2;
        `:`
            margin-right: 15px;
            order: 1;
        `}
        ${props => !props.content && `
            margin-left: 0px;
            margin-right: 0px;
        `}
        & + span {
            font-size: 15px !important;
            order: 1;
        }
    }
`


export {
    Container
}
