import React from 'react'
import {
    Switch,
    Route
} from 'react-router-dom'
import DefaultSettings from './default'
import { AutoanswerPage } from './auto-answer'
import { TriggerPage } from './triggers'
import { RecordPage } from './records'
import { StatPage } from './stat'
import { ShortcodePage } from './shortcodes'
import NavLinks from './navLinks'
import Menu from 'components/menu'
import {
    Content,
    Container
} from './styles'

import {
    Flex
} from 'styles'

const Settings = () => {

    return (
        <Container>
            <Flex align="flex-start" wrap="no-wrap" justify="space-between">
                <Menu>
                    <div className="menu">
                        <NavLinks />
                    </div>
                    <Content className="content">
                        <Switch>
                            <Route path="/settings/auto-answer">
                                <AutoanswerPage />
                            </Route>
                            <Route path="/settings/triggers">
                                <TriggerPage />
                            </Route>
                            <Route path="/settings/records">
                                <RecordPage />
                            </Route>
                            <Route path="/settings/stat">
                                <StatPage />
                            </Route>
                            <Route path="/settings/shortcodes">
                                <ShortcodePage />
                            </Route>
                            <Route path="/settings">
                                <DefaultSettings />
                            </Route>
                        </Switch>
                    </Content>
                </Menu>
            </Flex>
        </Container>
    )
}

export default Settings
