import styled from 'styled-components'

const Container = styled.div`
    margin: var(--spacing) 0px;
    animation: fade-in .3s forwards ease-in-out;
`

const Content = styled.aside`
    box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.03);
    padding: 10px 15px;
    border-radius: 10px;
    background-color: #fff;
    overflow-x: hidden;
    @media (max-width: 500px) {
        padding: 10px 15px;
    }
`

const Title = styled.h3`
    display: block;
    margin: ${props => props.margin || '10px 5px'};
    font-size: 20px;
    letter-spacing: 0.01em;
`

const Caption = styled.div`
    font-size: 15px;
    margin: 5px;
    opacity: .5;
    line-height: 22px;
`

const NavWrapper = styled.div`
    display: flex;
    flex-direction: column;
    & a, button {
        display: grid;
        grid-template-columns: 35px 1fr;
        text-align: left;
    }
`

const Tools = styled.div`
    overflow-x: hidden;
    animation: slide-left .2s forwards ease-in-out;
`


export {
    Container,
    Content,
    Title,
    Caption,
    NavWrapper,
    Tools,
}
