import styled from 'styled-components'

const Container = styled.header`
    padding: 10px;
    box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.03);
    border-radius: 0px 0px 10px 10px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap;
    animation: slide-down .3s forwards ease-in-out;
    z-index: 10;
    & img {
        width: 20px;
        height: 20px;
        pointer-events: none;
    }
    & .logo-icon {
        margin-right: auto;
        margin-left: 10px;
        opacity: .5;
        & a {
            display: block;
        }
        & img {
            width: 70px;
            height: 50px;
        }
    }
`

export {
    Container
}
