import styled from 'styled-components'

const Container = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 102;
    transition: .3s;
    animation: fade-in .1s forwards ease-in-out;
    overflow-y: hidden;
`

const Content = styled.div`
    max-width: 90%;
    max-height: 90vh;
    overflow-y: auto;
    padding: ${props => props.padding ? props.padding : '25px'};
    background-color: #fff;
    border-radius: 10px;
    @media (max-width: 500px) {
        width: 90%;
        padding: 15px;
    }
`

export {
    Container,
    Content
}
