import React from 'react'
import {
    Switch,
    Route
} from 'react-router-dom'
import { ShortcodeFaq, ShortcodeList, ShortcodeTools } from '.'
import {
    Title,
} from '../styles'
import {
    Flex,
} from 'styles'


const ShortcodePage = () => {

    return (
        <>
            <Flex justify='space-between' align="center" margin="0px">
                <Title margin="10px 5px">Шорткоды</Title>
                <ShortcodeFaq />
            </Flex>
            <Switch>
                <Route path="/settings/shortcodes/tools">
                    <ShortcodeTools />
                </Route>
                <Route path="/settings/shortcodes" exact>
                    <ShortcodeList />
                </Route>
            </Switch>
        </>
    )
}

export {
    ShortcodePage
}

