import React from 'react'
import { NavLink } from 'react-router-dom'
import getIcon from 'components/icons'
import { ShortcodeToolItem } from '..'

import {
    Tools
} from '../../styles'

import {
    Wrapper,
    Title,
    Grid,
    Divider
} from 'styles'

const ShortcodeTools = () => {

    return (
        <Tools>
            <NavLink to="/settings/shortcodes" activeClassName="" className="link-button gray">
                <img src={getIcon('back')} alt="get back"/>
                <span>Назад</span>
            </NavLink>
            <Divider className="main" margin="25px 0px 20px 0px" opacity=".2" />
            <Wrapper display="block" shadow="none" margin="0px" padding="0px" >
                <Title margin="10px 0px">Классификаторы</Title>
                <Grid gap="20px">
                    {
                        <>
                            <ShortcodeToolItem />
                            <ShortcodeToolItem />
                        </>
                    }
                </Grid>
            </Wrapper>
            <Divider className="main" margin="25px 0px 20px 0px" opacity=".2" />
            <Wrapper display="block" shadow="none" margin="0px" padding="0px" >
                <Title margin="10px 0px">Идентификаторы</Title>
                <Grid gap="20px">
                    {
                        <>
                            <ShortcodeToolItem />
                            <ShortcodeToolItem />
                        </>
                    }
                </Grid>
            </Wrapper>
        </Tools>
    )
}

export {
    ShortcodeTools
}
