import React from 'react'
import {
    Switch,
    Route
} from 'react-router-dom'
import Header from 'components/header'
import Settings from 'pages/settings'
import Chat from 'pages/chat'


const Main = () => {

    return (
        <>
            <Switch>
                <Route path="/settings">
                    <Settings />
                </Route>
                <Route path="/user">
                    User
                </Route>
                <Route path="/chat">
                    <Chat />
                </Route>
                <Route path="/" exact>
                    <Header />
                </Route>
            </Switch>
        </>
    )
}

export default Main