import React, { useState } from 'react'
import { useHistory } from "react-router-dom";
import Button from 'components/button'
import { Input } from 'components/input'
import {
    Container,
    Header,
    Counter
} from './styles'
import {
    Title,
} from 'styles'

const LogIn = () => {
    const history = useHistory()
    const [phone] = useState('998343315703')
    const [onError, setOnError] = useState(true)
    const [order, setOrder] = useState(0)

    const steps = [
        <>
            <Input
                autoFocus={true}
                required={false}
                autoComplete="off"
                onError={setOnError}
                regExp={/^\d*$/}
                label="Придумайте пароль"
                caption="Введите что угодно, только не это!"
                type="text"
                name="text"
                placeholder="пароль123"
                margin="10px auto 5px auto"
                className="main"
            />
            <Input
                required={false}
                autoComplete="off"
                onError={setOnError}
                regExp={/^\d*$/}
                label="Номер телефона"
                caption="Введите только цифры, включая код страны"
                type="text"
                name="text"
                placeholder="998434335703"
                margin="10px auto 5px auto"
                className="main"
            />
        </>,
        <Input
            required={false}
            autoFocus={true}
            autoComplete="off"
            onError={setOnError}
            regExp={/^\d{5}$/}
            label={
                <>
                    Код с телеграма&nbsp;
                    <span className="main">+{phone}</span>
                </>
            }
            caption="Неверный формат, пароль состоит из 5 цифр"
            type="text"
            name="password"
            placeholder="Введите полученный пароль из тг"
            margin="10px auto 5px auto"
        />
    ]

    const makeUpdate = () => {
        // if ... else ...
        setOnError(true)
    }

    const cancelOrder = (e) => {
        setOrder(() => {
            return order - 1
        })
        e.target.closest('form').reset()
    }

    const onSubmit = (e) => {
        e.preventDefault()
        if (!onError) {
            if (order + 1 === steps.length) {
                history.push('/main')
                return false
            }
            e.target.reset()
            setOrder(() => {
                return order + 1
            })
        }
        makeUpdate()
    }

    return (
        <Container onSubmit={onSubmit}>
            <Header>
                {order > 0 ?
                    <Button type="button" onClick={cancelOrder} className="under" padding="0px">Назад</Button>
                    :
                    <span></span>
                }
                <Title size='medium' align="center" padding="0px 20px">
                    Авторизация
                </Title>
                <Counter>{order + 1}/{steps.length}</Counter>
            </Header>
                {steps[order]}
                <Button
                    isLoading={onError}
                    margin='20px auto'
                    disable={onError}
                    className='main'
                >
                    Готово
                </Button>
        </Container>
    )
}

export default LogIn