import styled from 'styled-components'

const fontSize = {
    'bold': '32px',
    'medium': '24px',
    'regular': '18px',
    'small': '14px'
}


const Wrapper = styled.div`
    margin: ${props => props.margin ? props.margin : '0px auto'};
    padding: ${props => props.padding ? props.padding : '20px'};
    border-radius: ${props => props.radius ? props.radius : '7px'};
    box-shadow: ${props => props.shadow ? props.shadow : '0px 0px 5px 1px rgb(0 0 0 / 10%)'};
    max-width: ${props => props.maxWidth ? props.maxWidth : '100%'};
    min-width: ${props => props.minWidth ? props.minWidth : 'auto'};
    display: ${props => props.display ? props.display : 'flex'};
    /* grid-template-columns: ${props => props.columns || '1fr 1fr 1fr'}; */
    width: ${props => props.width ? props.width : 'auto'};
    z-index: ${props => props.zIndex ? props.zIndex : 'inherit'};
    flex-wrap: ${props => props.wrap ? props.wrap : 'wrap'};
    background-color: inherit;
    align-items: ${props => props.align ? props.align : 'strecth'};
    flex-direction: ${props => props.direction ? props.direction : 'row'};
    justify-content: ${props => props.justify ? props.justify : 'none'};
    animation: ${props => props.animation ? props.animation : 'none'};
    transition: .3s;
`

const Title = styled.h3`
    display: block;
    font-size: ${props => props.size ? fontSize[props.size] ? fontSize[props.size] : props.size : '16px'};
    text-align: ${props => props.align ? props.align : 'left'};
    text-transform: ${props => props.transform ? props.transform : 'none'};
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.padding ? props.padding : '0px'};
    color: ${props => props.color ? props.color : '#000'};
    font-weight: ${props => props.weight ? props.weight : 'normal'};
    letter-spacing: 0.02em;
`

const SubTitle = styled.span`
    display: block;
    font-size: ${props => props.size ? fontSize[props.size] ? fontSize[props.size] : props.size : '17px'};
    text-align: ${props => props.align ? props.align : 'left'};
    text-transform: ${props => props.transform ? props.transform : 'none'};
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.padding ? props.padding : '0px'};
    color: ${props => props.color ? props.color : '#000'};
    font-weight: ${props => props.weight ? props.weight : 'normal'};
    letter-spacing: 0.02em;
`

const Link = styled.a`
    font-size: ${props => props.size ? fontSize[props.size] ? fontSize[props.size] : props.size : '16px'};
    text-transform: ${props => props.transform ? props.transform : 'none'};
    margin: ${props => props.margin ? props.margin : '0px'};
    padding: ${props => props.padding ? props.padding : '0px'};
    color: #5DA3C0;
    font-weight: ${props => props.weight ? props.weight : 'normal'};
    letter-spacing: 0.02em;
`

const Flex = styled.div`
    width: ${props => props.width ? props.width : 'auto'};
    display: flex;
    flex-wrap: ${props => props.wrap ? props.wrap : 'wrap'};
    margin: ${props => props.margin ? props.margin : '0px auto'};
    align-items: ${props => props.align ? props.align : 'strecth'};
    flex-direction: ${props => props.direction ? props.direction : 'row'};
    justify-content: ${props => props.justify ? props.justify : 'none'};
`

const Grid = styled.div`
    width: ${props => props.width ? props.width : 'auto'};
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(285px,1fr));
    grid-gap: ${props => props.gap ? props.gap : '0px'};
    padding: 15px 5px;
    @media (max-width: 500px) {
        grid-template-columns: repeat(auto-fill,minmax(200px,1fr));
    }
`

const FormItem = styled.form`
    display: flex;
    flex-direction: column;
    padding: 10px;
    border-radius: 7px;
    background-color: #fff;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
    animation: scale-down .2s forwards ease-in-out;
    will-change: transform;
    &.deleting {
        pointer-events: none;
        transition: 1s;
        animation: deleting .7s forwards ease-in-out;
    }
`

const FAQ = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    padding: 5px 7px;
    & img {
        width: 30px;
        height: 30px;
        cursor: pointer;
        opacity: .5;
    }
`

const FaqContent = styled.div`
    max-width: 700px;
`

const BreadcrumbsList = styled.div`
    margin: ${props => props.margin || '0px'};
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    & a {
        font-size: 15px;
        padding-right: 15px;
        opacity: .5;
        position: relative;
    }
    & a:after {
        content: '/';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 3px;
        opacity: .5;
    }
    & a:last-child:after {
        display: none;
    }
    & a:hover {
        opacity: 1;
    }
`

const Divider = styled.div`
    display: block;
    border-radius: 50%;
    margin: ${props => props.margin || '0px'};
    opacity: ${props => props.opacity || '1'};
    height: 2px;
    border: 1px solid;
`

const Sticky = styled.div`
    position: sticky;
    display: inline-block;
    top: ${props => props.top || 'auto'};
    bottom: ${props => props.bottom || 'auto'};
    z-index: 100;
`

const Fixed = styled.div`
    position: fixed;
    top: ${props => props.top || 'auto'};
    left: ${props => props.left || 'auto'};
    right: ${props => props.right || 'auto'};
    bottom: ${props => props.bottom || 'auto'};
    z-index: 100;
    border: 5px solid #fff;
    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
`

export {
    Wrapper,
    Title,
    SubTitle,
    Link,
    Flex,
    Grid,
    FormItem,
    FAQ,
    FaqContent,
    BreadcrumbsList,
    Divider,
    Sticky,
    Fixed
}

